.footer {
  display: block;
  text-align: center;
  height: 30px;
  border-top: 2px solid #141414;
  padding-bottom: 10px;
  padding-top: 10px;
  width: 95%;
  margin: 0 auto;
  /* Workaround for iPhone 6/7/8 & Galaxy S5*/
  /* Workaround for iPhone 5/SE*/ }
  @media screen and (max-height: 500px) {
    .footer {
      position: absolute; } }
  @media screen and (max-height: 700px) and (max-width: 400px) {
    .footer {
      padding-top: 10px; } }
  @media screen and (max-height: 600px) and (max-width: 400px) {
    .footer {
      padding-top: 40px; } }
  .footer__container {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between; }
  .footer__start, .footer__center, .footer__end {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center; }
  .footer__text {
    color: white;
    display: inline;
    font-size: 18px;
    font-family: "Calibri";
    display: block;
    font-weight: lighter;
    cursor: pointer;
    font-style: italic;
    -webkit-animation: text-flicker 6s linear infinite;
            animation: text-flicker 6s linear infinite; }
    @media screen and (max-width: 800px) {
      .footer__text {
        font-size: 15px; } }
    .footer__text--1 {
      margin-bottom: 5px; }
    .footer__text--2 {
      -webkit-align-items: center;
              align-items: center; }
    .footer__text--3 {
      color: grey;
      text-decoration: underline; }
  .footer__link {
    text-decoration: underline; }
  .footer__logo-tmdb {
    width: 90px;
    cursor: pointer;
    margin-top: 30px; }
  .footer__logo-github {
    width: 50px;
    cursor: pointer;
    margin-top: 20px; }
  .footer__icon-heart-o {
    margin-left: 5px;
    margin-bottom: 0px;
    text-shadow: -0.1vw 0vw 0.25vw #fbb4ff, -0.2vw 0vw 0.25vw #fbb4ff, -0.4vw 0vw 0.25vw #fbb4ff, -0.1vw 0vw 1vw #e82ff5, -0.2vw 0vw 0.8vw #e82ff5, -0.4vw 0vw 0.8vw #e82ff5, 0.2vw 0vw 0.5vw #e82ff5, -0.1vw 0vw 10vw #cb16fa, -0.2vw 0vw 10vw #cb16fa, -0.4vw 0vw 10vw #cb16fa; }
  .footer__icon-heart-off {
    animation: heart 0.1s ease infinite;
    -moz-animation: heart 0.1s ease infinite;
    /* Firefox */
    -webkit-animation: heart 0.1s ease infinite;
    /* Safari and Chrome */
    -o-animation: heart 0.1s ease infinite;
    /* Opera */ }
  .footer .react {
    display: inline-block; }

